*{margin: 0px;padding: 0px;}
.slide{
    width: 1900px;
    height: 370px;
    background-color: #000;
    margin: 0px auto;
    position: relative;
}
.slide img{
    position: absolute;
}
.shop{
    width: 100%;
    position: relative;
    margin-top: 140px;
}
.shop h1{
    position: absolute;
    transform: translateY(-70px);
   
    left: 43%;
}
.shop img{
    margin: 0px auto;
    width: 250px;
    position: absolute; 
}
.shop img:first-child{
    left: 20%;
}
.shop img:nth-child(2){
    left: 35%;
}
.shop img:nth-child(3){
    left: 50%;
}
.shop img:last-child{
    left: 65%;
}